import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { fetchTrendingMovies } from 'services/api';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import styles from './Home.module.css';
const {
  container,
  title,
  tranding_card,
  tranding_card_link,
  tranding_card_img,
  tranding_card_info,
  tranding_card_title,
} = styles;

const Home = () => {
  const [trendingMovies, setTrendingMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const location = useLocation();

  useEffect(() => {
    setIsLoading(true);
    async function fetchTrending() {
      try {
        const movies = await fetchTrendingMovies();
        setTrendingMovies(movies);
      } catch (error) {
        console.log(error);
      } finally {
        setIsLoading(false);
      }
    }

    if (trendingMovies.length === 0) {
      fetchTrending();
    }
  }, [trendingMovies]);

  return (
    <div className={container}>
      <h1 className={title}>Tranding today</h1>
      {!isLoading ? (
        <div>Loading...</div>
      ) : (
        <Slider
          slidesToShow={1}
          slidesToScroll={1}
          dots={true}
          infinite={true}
          style={{ marginBottom: '100px' }}
        >
          {trendingMovies.map(movie => (
            <div key={movie.id} className={tranding_card}>
              <img
                src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                alt={movie.original_title}
                className={tranding_card_img}
              />
              <div className={tranding_card_info}>
                <h2 className={tranding_card_title}>{movie.title}</h2>
                <Link
                  key={movie.id}
                  to={`/movies/${movie.id}`}
                  state={{ from: location }}
                  className={tranding_card_link}
                >
                  view info
                </Link>
              </div>
            </div>
          ))}
        </Slider>
      )}
    </div>
  );
};

export default Home;
